.data {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    min-width: 270px;
    align-items: flex-start;
    margin-bottom: 20px;
}
.inputBlock {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}
.input {
    width: 45%;
}
@media screen and (max-width: 460px){
    .inputBlock {
        flex-direction: column;
    }
    .input {
        width: 97%;
        margin-bottom: 10px;
    }
}
.text {
    max-height: 300px;
    margin: 30px 0 20px 0;
    padding-top: 10px;
    width: 98%;
    height: 100px;
    resize: none;
}
.button {
    font-family: 'Montserrat', sans-serif;
    border: 0 none;
    border-radius: 0;
    height: 40px;
    padding: 0 40px;
    text-transform: uppercase;
    transition: all 0.3s ease 0s;
    cursor: pointer;
}